<template>
	<view>
		<uni-file-picker v-model="imageValue" fileMediatype="image" mode="grid" @select="onSelect" @progress="progress"
			@success="success" @fail="fail" limit="2" file-mediatype="image" >
		</uni-file-picker>

	</view>

</template>

<script setup lang="ts">
	import { FileMessage, UploadFiles } from "@/pages/index/indexClass"
	import { ref, watch } from 'vue';
	import { baseUrl } from "@/url/url"
	import methods from '@/pages/index/indexMethods'
	let { select, progress, success, fail, imagesUpload } = methods()






	const imageValue = ref<FileMessage[]>([]);
	let imageStyles = {
		width: 64,
		height: 64,
		border: {
			radius: '50%'
		}
	}
	function onSelect(e : any) {
		imageValue.value = select(e)
	}
	let name = ""
	function onImagesUpload() {
		const uploadFilesList : UploadFiles[] = []
		for (let i = 0; i < imageValue.value.length; i++) {
			uploadFilesList[i].filePath = imageValue.value[i].filePath;
			uploadFilesList[i].name = name;
			uploadFilesList[i].url = baseUrl;
			uploadFilesList[i].timeout = 60000;

		}
		imagesUpload(uploadFilesList)
	}
	for (let i = 0; i < imageValue.value.length; i++){
		uni.__f__('log','at pages/index/index.vue:47',imageValue.value[i].filePath)
	}
</script>

